
































.real-time-data {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.real-time-data h3 {
  margin: 0 0 16px;
  font-size: 18px;
  color: #333;
}

.data-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.data-item {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: #666;
}

.data-label {
  font-weight: bold;
  color: #333;
}

.data-value {
  color: #007bff;
}
